<template>
  <div>
    <button @click="back">回首頁</button>
    <div v-for="item in $store.state.memberList">
      姓名：{{item.name}}
      權限：{{item.permission}}
    </div>
    姓名：<input type="text" v-model="name" style="width: 60px"/>
    權限：
    <select v-model="permission">
      <option value="3">3</option>
      <option value="2">2</option>
      <option value="1">1</option>
    </select>
    <button @click="addmember">add</button>
  </div>
</template>

<script>
  export default {
    name: "editmember.vue",
    data() {
      return{
        name: '',
        permission: '3'
      }
    },
    methods: {
      addmember() {
        let obj = {
          name: this.name,
          permission: this.permission
        }
        this.$store.state.memberList.push(obj)
      },
      back() {
        this.$router.push({
          name: 'HelloWorld'
        })
      }
    }
  }
</script>

<style scoped>

</style>



