// store/index.js

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// state 類似全域變數的概念
const state = {
  user: '',
  age: '20',
  memberList: [
    {name: '小明', permission: 3},
    {name: '小美', permission: 2},
    {name: '阿傑', permission: 1},
    {name: '東東', permission: 2}
  ],
}

// 負責接收 action(commit) 資料並計算邏輯後改變 State
const mutations = {
  loginMember(state, data) {
    state.user = data
  },

}

// 處理非同步的事件，再利用 commit 與 mutation 溝通
const actions = {
  login(context, data) {
    setTimeout(() => {
      let user = ''
      if ((data.account == 'abc', data.password == '456')||(data.account == 'admin', data.password == 'admin')) {
        switch (data.account) {
          case "abc":
            user = 'ming'
            break
          case "admin":
            user = 'admin'
            break
        }
        context.commit('loginMember', user)
      } else {
        alert('帳號密碼有誤')
      }
    }, 1000)
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})





